<template>
  <div id="mj-container">
    <div>
      
    </div>
    <div class="swiper-container" v-if="picList.length">
      <swiper :options="swiperOption" ref="picList">
        <swiper-slide
          v-for="(item, index) in picList">
          <img :src="item.src" alt="" @click="showPhoto(index)">
        </swiper-slide>
        <div class="swiper-pagination" slot="pagination"></div>
      </swiper>
    </div>
    <div class="room-intro">
      <p class="room-price">
        {{price}}元/月起
        <span class="pay-way" @click="$modal.show('payway-modal')">付款方式<i class="icon"></i></span>
      </p>
      <p class="room-name">{{name}}</p>
    </div>
    <div class="room-intro">
      <div class="room-intro-title">房源信息</div>
      <div class="room-info-list">
        <div>
          <span>户型</span> {{houseType}}
        </div>
        <div>
          <span>装修</span> {{decorationDegree}}
        </div>
        <div>
          <span>面积</span> {{roomArea}}m²
        </div>
        <div>
          <span>朝向</span> {{roomDirection}}
        </div>
        <div>
          <span>楼层</span> {{floor}}层
        </div>
        <div>
          <span>编号</span> {{roomCode}}
        </div>
      </div>
    </div>
    <div class="room-intro">
      <div class="room-intro-title">房间设施</div>
      <div class="facility-list">
        <div 
          class="facility-item"
          v-for="(item, index) in showDeviceList">
          <div v-if="item.type == -1" @click="$modal.show('device-modal')">
            <img :src="require(`../assets/images/device-${item.type}.png`)" alt="">
            <p>{{item.name}}</p>
          </div>
          <div v-else>
            <img :src="require(`../assets/images/device-${item.type}.png`)" alt="">
            <p>{{item.name}}</p>
          </div>
        </div>
      </div>
    </div>
    <div class="room-intro">
      <div class="room-intro-title">房间描述</div>
      <div class="room-description" :class="[isMuchWords && !showMuchWords ? 'text-break' : '']" v-html="roomDesc" ref="roomDesc">
      </div>
      <div class="switch-description" v-if="isMuchWords">
        <span @click="showMuchWords=!showMuchWords">详情</span>
      </div>
    </div>
    <div class="room-intro">
      <div class="room-intro-title">地理位置</div>
      <p class="room-address">{{address}}</p>
      <div id="map_content" class="map_content"></div>
    </div>
    <div class="room-intro">
      <div class="room-intro-title">为您推荐</div>
      <ul class="similar-room-list">
        <li 
        class="similar-room-item"
        v-for="item in similarRoomList">
          <img :src="item.pic" alt="">
          <p>{{item.price}}元/月起</p>
          <p class="similar-room-address">{{item.address}}</p>
        </li>
      </ul>
    </div>
    <div class="fixed-footer">
      <a class="btn btn-default" href="tel:18815287483">联系房东</a>
      <span class="btn btn-primary">预约看房</span>
      <span class="btn btn-info">在线预订</span>
    </div>
    <modal 
      class="mj-modal payway-modal" 
      name="payway-modal"
      width="100%"
      height="100%">
      <div class="modal-head">
        付款方式
        <span class="close-btn" @click="$modal.hide('payway-modal')"></span>
      </div>
      <div class="modal-container">
        <ul>
          <li 
          class="modal-list-item payway-item"
          v-for="item in payway">
            <div class="f-row">
              <span>{{item.name}}</span>
              <span>¥ {{item.price}} /月起</span>
            </div>
            <div class="s-row">
              {{item | paywayFormat}}
            </div>
          </li>
        </ul>
      </div>
    </modal>
    <modal 
      class="mj-modal" 
      name="device-modal"
      width="100%"
      height="100%">
      <div class="modal-head">
        房间设施
        <span class="close-btn" @click="$modal.hide('device-modal')"></span>
      </div>
      <div class="modal-container">
        <ul>
          <li 
          class="modal-list-item"
          v-for="item in deviceList"
          >
            <div class="device-row">
              <span>{{item.name}}</span>
              <span><img :src="require(`../assets/images/device-${item.type}.png`)" alt=""></span>
            </div>
          </li>
        </ul>
      </div>
    </modal>
    <!-- Root element of PhotoSwipe. Must have class pswp. -->
    <div ref="pswp" class="pswp" tabindex="-1" role="dialog" aria-hidden="true">

        <!-- Background of PhotoSwipe. 
             It's a separate element as animating opacity is faster than rgba(). -->
        <div class="pswp__bg"></div>

        <!-- Slides wrapper with overflow:hidden. -->
        <div class="pswp__scroll-wrap">

            <!-- Container that holds slides. 
                PhotoSwipe keeps only 3 of them in the DOM to save memory.
                Don't modify these 3 pswp__item elements, data is added later on. -->
            <div class="pswp__container">
                <div class="pswp__item"></div>
                <div class="pswp__item"></div>
                <div class="pswp__item"></div>
            </div>

            <!-- Default (PhotoSwipeUI_Default) interface on top of sliding area. Can be changed. -->
            <div class="pswp__ui pswp__ui--hidden">

                <div class="pswp__top-bar">

                    <!--  Controls are self-explanatory. Order can be changed. -->

                    <div class="pswp__counter"></div>

                    <button class="pswp__button pswp__button--close" title="Close (Esc)"></button>

                    <button class="pswp__button pswp__button--share" title="Share"></button>

                    <button class="pswp__button pswp__button--fs" title="Toggle fullscreen"></button>

                    <button class="pswp__button pswp__button--zoom" title="Zoom in/out"></button>


                    <div class="pswp__preloader">
                        <div class="pswp__preloader__icn">
                          <div class="pswp__preloader__cut">
                            <div class="pswp__preloader__donut"></div>
                          </div>
                        </div>
                    </div>
                </div>

                <div class="pswp__share-modal pswp__share-modal--hidden pswp__single-tap">
                    <div class="pswp__share-tooltip"></div> 
                </div>

                <button class="pswp__button pswp__button--arrow--left" title="Previous (arrow left)">
                </button>

                <button class="pswp__button pswp__button--arrow--right" title="Next (arrow right)">
                </button>

                <div class="pswp__caption">
                    <div class="pswp__caption__center"></div>
                </div>

            </div>

        </div>

    </div>
  </div>
</template>

<script>

const DecorationList = ['', '毛坯', '简装', '精装修', '豪华装'];
const RoomDirection = ['', '朝南', '朝北', '朝东', '朝西', '东南', '西南', '东北', '西北'];

export default {
  data() {
    return {
      type: null,
      roomId: null,
      picList: [],
      name: '',
      price: '',
      houseType: '',
      decorationDegree: '',
      roomArea: '',
      roomDirection: '',
      floor: '',
      roomCode: '',
      roomDesc: '',
      address: '',
      coordinate: [],
      payway: [],
      similarRoomList: [],
      deviceList: [],
      swiperOption: {
        loop : true,
        autoplay: {
          disableOnInteraction: false
        },
        pagination: {
          el: '.swiper-pagination',
          type: 'fraction'
        },
      },
      isMuchWords: false,
      showMuchWords: false
    }
  },
  computed: {
    showDeviceList() {
      if(this.deviceList.length > 6){
        let arr = this.deviceList.slice(0, 5);
        arr.push({
          type: -1,
          name: '更多'
        });
        return arr;
      } else {
        return this.deviceList;
      }
    }
  },
  methods: {
    initRoomInfo() {
      if(location.search){
        let searchObj = {};
        let searchArr = location.search.slice(1).split('&');
        for (let i = 0; i < searchArr.length; i++) {
          if(searchArr[i].split("=")[1]){
            searchObj[searchArr[i].split("=")[0]] = unescape(searchArr[i].split("=")[1]);
          }
        }
        this.type = searchObj['type'];
        this.roomId = searchObj['roomId'];
      }
    },
    getRoomInfo() {
      this.sendReq('house', {
        "v": "1.0",
        "method": "queryHouseRoomDetail",
        "params": {
          "devId": "5555998cccf2492db015c442f087f00a",
          "roomId": "55234"
        }
      }).then((res) => {
        console.log(res)
        if(res.data.code == 0){
          res.data.data.similarRoomList.forEach((item, index) => {
            this.similarRoomList.push({
              id: item.roomId,
              pic: item.imageUrl,
              price: item.minRentPrice,
              address: item.region
            });
          });
          res.data.data.images.forEach((item, index) => {
            this.picList.push({
              src: item,
              w: 375,
              h: 280
            });
          });
          res.data.data.rentTypes.forEach((item, index) => {
            this.payway.push({
              name: item.name,
              price: item.rentPrice,
              deposit: item.depositPrice,
              period: [item.period[0], item.period[item.period.length - 1]],
              serviceFee: item.serviceChargePrice
            });
          });
          res.data.data.privateFacilityItems.forEach((item, index) => {
            this.deviceList.push({
              type: item.type,
              name: item.name
            });
          });
          this.price = res.data.data.price;
          this.name = res.data.data.houseName;
          this.roomDesc = res.data.data.houseDesc;
          this.houseType = res.data.data.houseType;
          this.decorationDegree = DecorationList[res.data.data.decorationDegree];
          this.roomArea = res.data.data.houseArea;
          this.roomDirection = RoomDirection[res.data.data.roomDirection];
          this.floor = res.data.data.floorName;
          this.roomCode = res.data.data.roomCode;
          this.longitude = res.data.data.longitude;
          this.latitude = res.data.data.latitude;
          this.address = res.data.data.address;
          this.coordinate.push(res.data.data.longitude);
          this.coordinate.push(res.data.data.latitude);
        }
      }).catch((err) => { console.log(err) })
    },
    showPhoto(i) {
      var options = {
        index: i
      };
      this.openPhotoSwipe(this.$refs.pswp, this.picList, options);
    }
  },
  filters: {
    paywayFormat(o) {
      return `租期:${o.period[0]}~${o.period[1]}个月  |  押金:¥${o.deposit} 起  |  服务费:¥${o.serviceFee}/月 起`;
    }
  },
  watch: {
    roomDesc() {
      this.$nextTick(() => {
        let rowHeight = document.documentElement.clientWidth / 10 * 0.48;
        if(this.$refs.roomDesc.offsetHeight > rowHeight * 3){
          this.isMuchWords = true;
        }
      });
    },
    coordinate(val) {
      /* 地图信息 */
      var map = new BMap.Map("map_content");
      var point = new BMap.Point(val[0],val[1]);
      map.centerAndZoom(point, 15);
      var marker = new BMap.Marker(point);  // 创建标注
      map.addOverlay(marker);               // 将标注添加到地图中
      map.disableDragging();
      map.disableScrollWheelZoom();
      map.disableDoubleClickZoom();
      marker.setAnimation(BMAP_ANIMATION_BOUNCE); //跳动的动画
    }
  },
  created() {
    this.initRoomInfo();
    this.getRoomInfo();
  },
  mounted() {
    this.$modal.show('swiper-modal');
  }
}
</script>

<style lang="scss">
@mixin bgFill($url) {
  background-image: url($url);
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: center;
}
@mixin multiple-text-break($line: 2) {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  /* autoprefixer: off */
  -webkit-line-clamp: $line;
  /* autoprefixer: on */
  overflow: hidden;
}
* {
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  -webkit-tap-highlight-color: transparent; /* For some Androids */
  outline: none;
}
*,
*::before,
*::after {
    box-sizing: border-box;
}
::-webkit-scrollbar {
  display: none;
}
input,textarea,button { border: 0; margin: 0; padding: 0; font-family:"微软雅黑", "Helvetica Neue", Helvetica, STHeiTi, Arial, sans-serif;}
textarea { resize: none;}
table { border-collapse: collapse; border-spacing: 0; }
td, th { padding: 0; }
h1, h2, h3, h4, h5, h6, p, figure, form, blockquote { margin: 0; }
ul, ol, li, dl, dd { margin: 0; padding: 0; }
ul, ol { list-style: none; }
input::-moz-placeholder, textarea::-moz-placeholder { color: #ccc; }
input:-ms-input-placeholder, textarea:-ms-input-placeholder { color: #ccc; }
input::-webkit-input-placeholder, textarea::-webkit-input-placeholder { color: #ccc; }
a {
  text-decoration: none;
}
html {
  font-family: 'Microsoft YaHei';
  line-height: 1;
}
body {
  margin: 0;
  padding: 0;
  padding-bottom: 1.28rem;
  font-size: 0.32rem;
  color: #333;
  background-color: #fff;
  -webkit-overflow-scrolling: touch;
}
.swiper-container {
  width: 100%;
  height: 7.493333rem;
  color: #fff;
  font-size: 0.373333rem;
  img {
    width: 100%;
    height: 100%;
  }
  .swiper-pagination {
    text-align: right;
    padding-right: 0.4rem;
  }
}
.room-intro {
  padding: 0.48rem 0;
  margin-left: 0.4rem;
  border-bottom: 1px solid #f2f2f2;
  font-size: 0.373333rem;
  .room-intro-title {
    font-size: 0.426667rem;
    font-weight: bold;
    margin-bottom: 0.48rem;
  }
  .room-price, .room-name {
    font-size: 0.48rem;
    line-height: 0.64rem;
  }
  .room-price {
    margin-bottom: 0.24rem;
    color: #ee7500;
    .pay-way {
      display: inline-block;
      float: right;
      margin-right: 0.4rem;
      font-size: 0.32rem;
      color: #ccc;
      .icon {
        display: inline-block;
        vertical-align: top;
        width: 0.373333rem;
        height: 0.64rem;
        background-image: url(../assets/images/icon-r.png);
        background-position: center;
        background-size: 100%;
        background-repeat: no-repeat;
      }
    }
  }
  .room-info-list {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: -0.32rem;
    div {
      width: 50%;
      margin-bottom: 0.32rem;
    }
    span {
      color: #ccc;
    }
  }
  .facility-list {
    text-align: center;
    .facility-item {
      width: 1.6rem;
      display: inline-block;
      img {
        width: 1.28rem;
      }
      p {
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
    }
  }
  .room-description {
    line-height: 0.48rem;
    white-space: pre-line;
    padding-right: 0.4rem;
    text-align: justify;
    &.text-break {
      @include multiple-text-break(3);
      margin-bottom: 0.24rem;
    }
  }
  .switch-description {
    text-align: center;
    span {
      display: inline-block;
      width: 1.6rem;
      height: 0.64rem;
      border-radius: 0.32rem;
      border: 1px solid #333;
    }
  }
  .similar-room-list {
    white-space: nowrap;
    overflow-x: scroll;
    overflow-y: hidden;
    font-size: 0;
  }
  .similar-room-item {
    display: inline-block;
    width: 6.4rem;
    font-size: 0.373333rem;
    margin-bottom: -0.24rem;
    margin-right: 0.4rem;
    img {
      width: 100%;
      margin-bottom: 0.24rem;
    }
    p {
      margin-bottom: 0.24rem;
    }
    .similar-room-address {
      color: #ccc;
    }
  }
  .map_content {
    width: 10.0rem;
    height: 7.2rem;
    margin-left: -0.4rem;
  }
  .room-address {
    margin-bottom: 0.32rem;
  }
}
.fixed-footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: #fff;
  padding: 0.16rem 0.24rem;
  display: flex;
  align-items: center;
  .btn {
    margin: 0 0.16rem;
    flex: 1;
    height: 0.96rem;
    line-height: 0.96rem;
    text-align: center;
    background-color: #ff00ff;
    border-radius: 0.16rem;
    color: #fff;
  }
  .btn-default {
    background-color: #f5f5f5;
    color: #333;
  }
  .btn-primary {
    background-color: #FFBE23;
  }
  .btn-info {
    background-color: #FF8C07;
  }
}
.mj-modal {
  .v--modal {
    border-radius: 0;
  }
  .modal-head {
    position: relative;
    font-size: 0.48rem;
    text-align: center;
    padding: 0.4rem;
    border-bottom: 1px solid #ddd;
    .close-btn {
      position: absolute;
      top: 0;
      left: 0;
      width: 1.28rem;
      height: 1.28rem;
      @include bgFill("../assets/images/close-btn.png");
    }
  }
}
.modal-list-item {
  padding: 0.4rem 0;
  margin: 0 0.4rem;
  border-bottom: 1px solid #ddd;
}
.payway-item {
  .f-row {
    display: flex;
    justify-content: space-between;
    font-size: 0.373333rem;
    margin-bottom: 0.24rem;
  }
  .s-row {
    color: #999;
  }
}
.device-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  img {
    width: 1.28rem;
  }
}
.ripple-div {
  width: 100%;
  height: 1.28rem;
  line-height: 1.28rem;
  text-align: center;
  padding: 0 0.4rem;
  background-color: #ff0000;
  color: #fff;
}
.swiper-modal {
  background-color: #000;
}
</style>



